/* ******** */
/* variable */
/* ******** */
:root {
  --TestBorder: 1px solid #000;

  --DefaultColor: #222222;
  --WhiteColor: #fefefe;
  --ImitationColor: #c8c8c8;
  --AlphaWhiteColor: rgba(254, 254, 254, 0.7);
  --OverlayBlackColor: rgba(3, 4, 4, 0.7);
  --OverlayBlackColorOff: rgba(3, 4, 4, 0);
  --ActiveOrange: #faae0b;
  --ErrorColor: #fd6060;
}
/* ******** */
/* ******** */
/* ******** */

/* *** */
/* env */
/* *** */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}

/* scrollbar */
::-webkit-scrollbar {
  width: 15px;
  height: 15px;
}
::-webkit-scrollbar-track {
  background: var(--AlphaWhiteColor);
}
::-webkit-scrollbar-thumb {
  background: var(--ActiveOrange);
  border-radius: 40rem;
}
/* ********* */

body {
  background: var(--WhiteColor);
  margin: 0;
  text-size-adjust: 100%;
  overflow: overlay;
}
.body {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
}

.head-line1,
.head-line2,
.button-text,
.normal-text,
.footer-text,
.imitation-text,
.error-text {
  color: var(--DefaultColor);
  font-family: "Yu Gothic", "游ゴシック", YuGothic, "游ゴシック体",
    "Hiragino Sans", "ヒラギノ角ゴシック", Meiryo, "メイリオ";
  font-weight: bold;
}
.head-line1 {
  font-size: 40px;
}
.head-line2 {
  font-size: 32px;
}
.button-text {
  font-size: 20px;
}
.normal-text {
  font-size: 16px;
}
.imitation-text {
  color: var(--ImitationColor);
  font-size: 16px;
}
.footer-text {
  color: var(--WhiteColor);
  font-size: 14px;
}
.error-text {
  color: var(--ErrorColor);
}

.non-select {
  user-select: none;
}

.head-line1 {
  width: 800px;
  height: 64px;
  margin: 0 auto;
}

.head-line2 {
  width: 720px;
  height: 48px;
  margin: 0 auto;
}

.head-line-2-sp {
  margin: initial;
  font-size: initial;
}
@media screen and (max-width: 900px) {
  .head-line-2-sp {
    font-size: 32px;
    display: inline-block;
    margin: 32px 0 0 0;
  }
}

.scroll-to-top {
  position: fixed!important;
  bottom: 2%!important;
  right: 8%!important;
  border-radius: 40px!important;
  width: 56px!important;
  height: 40px!important;
}
.scroll-to-top svg {
  fill: var(--ActiveOrange);
}
/* *** */
/* *** */
/* *** */

/* ****** */
/* header */
/* ****** */
.header {
  background: var(--AlphaWhiteColor);
  backdrop-filter: blur(16px);
  position: sticky;
  top: 0;
  width: 100%;
  max-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 800;
}

.header-wrapper {
  width: 1440px;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-wrapper {
  width: 320px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0 0 48px;
}
@media screen and (max-width: 909px) {
  .logo-wrapper {
    width: 216px;
    height: 64px;
    margin: 0;
    justify-content: center;
  }
}

.jiei-logo {
  width: 192px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease-in-out;
}
@media screen and (max-width: 900px) {
  .jiei-logo {
    scale: 0.9;
  }
}

.jiei-logo:hover {
  transform: scale(1.2);
}
@media screen and (max-width: 900px) {
  .jiei-logo:hover {
    transform: none;
  }
}

.logo-wrapper .active {
  pointer-events: none;
}

.pc-nav {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
@media screen and (max-width: 900px) {
  .pc-nav {
    display: none;
  }
}
.sp-nav {
  display: none;
}
@media screen and (max-width: 900px) {
  .sp-nav {
    width: 100px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 900;
  }
}

@media screen and (max-width: 900px) {
  .sp-nav .nav {
    width: 50vw;
    min-height: 100vh;
    min-height: 100dvh;
    display: flex;
    flex-flow: column;
    margin: 0 auto;
    text-align: left;

    position: fixed;
    top: 0;
    left: -100%;
    background: var(--DefaultColor);

    -webkit-transition: -webkit-transform 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    z-index: 900;
  }
  .sp-nav .show .nav {
    left: 0;
  }
}



@media screen and (max-width: 900px) {
  .drawer-overlay {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 100vh;
    min-height: 100vh;
    
    background: var(--OverlayBlackColorOff);
    pointer-events: none;

    -webkit-transition: -webkit-transform 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
  .show .drawer-overlay {
    background: var(--OverlayBlackColor);
    pointer-events: auto;
  }
}







.list-wrapper {
  display: flex;
}
@media screen and (max-width: 900px) {
  .list-wrapper {
    flex: 1;
    margin: 24px;
    position: relative;
    flex-direction: column;

    display: block;
    text-align: left;
    /* border: 1px solid #00f; */
  }
}

.list-item {
  width: 120px;
  height: 100px;
  display: flex;
}
@media screen and (max-width: 900px) {
  .list-item {
    width: 100%;
    height: 100%;
  }
  .button-text {
    color: var(--WhiteColor);
    border-bottom: 2px solid var(--WhiteColor);
  }
}

.list-item:nth-child(2n-1) {
  margin: 0 24px 0 24px;
}
@media screen and (max-width: 900px) {
  .list-item:nth-child(2n-1) {
    margin: 32px 0 32px 0;
  }
}

/* under line Animation */
.list-item .underline {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (max-width: 900px) {
  .list-item .underline {
    display: block;
  }
}

.list-item .underline::after {
  position: absolute;
  width: 120px;
  content: "";
  height: 8px;
  bottom: -1px;
  background: var(--ActiveOrange);
  transform: scale(0, 1);
  transform-origin: center top;
  transition: transform 0.2s ease-in-out;
}
.underline:hover::after {
  transform: scale(1, 1);
}
@media screen and (max-width: 900px) {
  .underline::after,
  .underline:hover::after {
    display: none;
  }
}
/* ******************** */

/* Dropdown menu */
.dropdown-list {
  display: none;
  position: absolute;
  pointer-events: none;
  color: var(--DefaultColor);
  top: 100%;
  margin: 0 auto;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.list-wrapper .underline:hover .dropdown-list,
.dropdown-menu:hover .dropdown-list,
.dropdown-list:hover .dropdown-list {
  display: block;
  pointer-events: auto;
  top: 100%;
  animation: fadeIn 0.4s ease-in 0s forwards;
}
@keyframes fadeIn {
  0% {
    display: none;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
@media screen and (max-width: 900px) {
  .list-wrapper .underline:hover .dropdown-list,
  .dropdown-menu:hover .dropdown-list,
  .dropdown-list:hover .dropdown-list {
    display: none;
  }
}

.dropdown-item {
  display: block;
  width: 100%;
  padding: 8px 24px 8px 24px;
  z-index: 500;
}

.dropdown-item:nth-child(1) {
  margin: 8px 0 0 0;
}
.dropdown-item:nth-child(2) {
  margin: 8px 0 8px 0;
}
.dropdown-item:nth-child(3) {
  margin: 8px 0 8px 0;
}
.dropdown-item:nth-child(4) {
  margin: 0 0 8px 0;
}

.dropdown-item:hover {
  background: var(--ActiveOrange);
  border-radius: 45px;
}
.dropdown-item:hover .button-text {
  color: var(--WhiteColor);
}
/* ************* */

.list-item .active,
.active .jiei-logo .logo {
  color: var(--ActiveOrange);
  fill: var(--ActiveOrange);
}
@media screen and (max-width: 900px) {
  .list-item .active {
    border-bottom: 2px solid var(--ActiveOrange);
  }
}

.icon {
  margin: 0 8px 0 0 ;
}

.github-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 900px) {
  .github-link {
    width: auto;
    height: auto;
    position: absolute;
    right: 0;
    bottom: 0;
  }
}

.github-icon {
  width: 64px;
  height: 64px;
  margin: 0 auto;
  color: var(--DefaultColor);
}
@media screen and (max-width: 900px) {
  .github-icon {
    color: var(--WhiteColor);
  }
}


/* hamburger-menu */
@media screen and (max-width: 900px) {
  .hamburger-menu {
    display: block;
    position: relative;
    width: 48px;
    height: 40px;
    -webkit-transition: -webkit-transform 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
}

@media screen and (max-width: 900px) {
  .hamburger-menu-bar {
    display: block;
    position: absolute;
    width: 48px;
    height: 7px;
    border-radius: 40px;
    background: var(--DefaultColor);
    -webkit-transition: -webkit-transform 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
}

.hamburger-menu-bar:nth-of-type(1) {
  top: 0;
}
.hamburger-menu-bar:nth-of-type(2) {
  top: 16px;
}
.hamburger-menu-bar:nth-of-type(3) {
  bottom: 0;
}

.active-toggle .hamburger-menu-bar {
  background: var(--WhiteColor);
}


/* Burger Animation  */
.active-toggle  {
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
}
.active-toggle .hamburger-menu-bar:nth-of-type(1) {
  -webkit-transform: translateY(17px) rotate(-45deg);
  transform: translateY(17px) rotate(-45deg);
}
.active-toggle .hamburger-menu-bar:nth-of-type(2) {
  -webkit-transform: translateY(0) rotate(45deg);
  transform: translateY(0) rotate(45deg);
}
.active-toggle .hamburger-menu-bar:nth-of-type(3) {
  opacity: 0;
}
