/* Modal */
.modal-overlay {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
  animation: modal-overlay 0.4s ease-in-out;
}
@keyframes modal-overlay {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.modal-content {
  background: var(--WhiteColor);
  padding: 10px;
  border-radius: 3px;
  scale: 2;
  animation: modal-content-pc 0.4s ease-in-out;
}
@keyframes modal-content-pc {
  0% {
    scale: 0;
  }
  100% {
    scale: 2;
  }
}
@media screen and (max-width: 900px) {
  .modal-content {
    width: 100%;
    scale: 1;
    animation: modal-content-sp 0.4s ease-in-out;
  }
  @keyframes modal-content-sp {
    0% {
      scale: 0;
    }
    100% {
      scale: 1;
    }
  }
}
